<template>
	<view class="container">
		<uni-collapse style="background-color: #E6E6FA;">
			<uni-collapse-item>
				<!-- 自定义折叠面板标题 -->
				<template v-slot:title>
					<view class="userinfo-header">
						<image :src="userData.img || '../../static/default-avatar.png'" class="avatar"></image>
						<view class="username">{{ userData.username }}</view>
					</view>
				</template>

				<!-- 折叠面板内容 -->
				<view class="userinfo-content">
					<view class="info-item">
						<text class="label">邮箱：</text>
						<text class="value">{{ userData.email || '未设置邮箱' }}</text>
					</view>

					<!-- 退出登录按钮 -->
					<button class="logout-btn" @click="logout">不玩了退了</button>
				</view>
			</uni-collapse-item>
		</uni-collapse>

		<view class="list">
			<view class="tab">
				<view v-for="(item, i) in navData" :key="i" :class="currentIndex === i ? 'current' : ''"
					@click="changeIndex(i)">
					{{ item }}
				</view>
			</view>
			<div class="item" v-for="item in orderData" :key="item.id">
				<image :src="item.product[0].pimageurl" mode=""></image>
				<view>
					<view>{{ item.product[0].pname }}</view>
					<view>下单时间:{{ item.otime }}</view>
					<view>花了我{{ item.oprice }}块</view>
				</view>
				<view>状态：{{ getStateText(item.ostate) }}</view>
				<button class="confirm-btn" @click="updateOrder(item.id)" v-if="item.ostate === 0">
					<uni-icons type="checkbox" size="15" color="#000"></uni-icons>
					<text>确认收货</text>
				</button>

			</div>
		</view>
	</view>
</template>

<script>
import {
	queryOrder,
	queryUserInfo,
	updateOrder,
} from "@/api/index";

export default {
	data() {
		return {
			navData: ["全部订单", "已收货", "未收货", "退订"],      // 导航栏数据
			//点击切换
			currentIndex: 0,
			teaToken: "", //用户认证信息
			userData: {}, //用户信息
			orderData: [] //用户订单信息
		}
	},
	onShow() {
		this.queryUserInfo()
		this.queryOrder()

	},
	methods: {

		// 切换导航项
		changeIndex(index) {
			this.currentIndex = index      // 更新当前选中项

			var ostate = ""      // 订单状态参数

			// 根据导航索引设置不同的订单状态
			if (index === 0) {
				ostate = ""
			} else if (index === 1) {
				ostate = "1"
			} else if (index === 3) {
				ostate = "2"
			} else if (index == 2) {
				ostate = "0"
			}
			// 请求对应状态的订单数据
			queryOrder(ostate).then(res => {
				this.orderData = res.data
			})

		},

		queryUserInfo() {
			queryUserInfo().then(res => {
				this.userData = res.data
			})
		},

		// 查询订单数据的方法
		queryOrder() {
			// 调用 api 中定义的 queryOrder 接口函数
			// 参数 "" 表示查询所有订单（不区分状态）
			queryOrder("").then(res => {
				// 请求成功后的回调
				// 将后台返回的订单数据赋值给 orderData
				this.orderData = res.data
				console.log('===== 订单列表数据 =====');
				console.log('订单数据：', res.data)  // 添加这行来查看数据

			})
		},

		//确认收货
		updateOrder(id) {
			uni.showModal({
				title: '确认收货',
				content: '确认已收到商品了吗？',
				success: (res) => {
					if (res.confirm) {
						// 查找当前订单的完整数据
						const currentOrder = this.orderData.find(item => item.id === id);
						if (!currentOrder) {
							uni.showToast({
								title: '订单数据错误',
								icon: 'error'
							});
							return;
						}

						// 更新订单状态
						const updateData = {
							...currentOrder,
							ostate: 1  // 确认收货状态，假设1为已收货
						};

						uni.request({
							url: '/userOrder',  // 发送到更新接口
							method: 'PUT',
							data: JSON.stringify(updateData),
							header: {
								'Content-Type': 'application/json'
							},
							success: (res) => {
								if (res.data.code === 200) {
									uni.showToast({
										title: '确认收货成功',
										icon: 'success'
									});
									this.queryOrder();  // 刷新订单列表
								} else {
									uni.showToast({
										title: '确认失败：' + (res.data.msg || '未知错误'),
										icon: 'error'
									});
								}
							},
							fail: (err) => {
								uni.showToast({
									title: '网络错误：' + err.errMsg,
									icon: 'error'
								});
							}
						});
					}
				}
			});
		},

		logout() {
			uni.showModal({
				title: '提示',
				content: '你确定退出登录吗',
				success: function (res) {
					if (res.confirm) {
						uni.removeStorage({
							key: 'token',
							success: function () {
								console.log('用户 token 已删除');
								// 跳转到登录页面或其他逻辑
							},
							fail: function (err) {
								console.error('删除用户 token 失败', err);
							}
						});
						uni.showToast({
							title: '用户已退出',
							duration: 2000
						});
						setTimeout(() => {

							uni.navigateTo({
								url: "/pages/login/login"
							})
						}, 2000)

						console.log('用户点击确定');
					} else if (res.cancel) {
						uni.showToast({
							title: '用户取消退出',
							duration: 2000
						});
						console.log('用户点击取消');
					}
				}
			});
		},

		// 添加状态文本转换方法
		getStateText(state) {
			const stateMap = {
				0: '未收货',
				1: '已收货',
				2: '退订',
				3: '禁用'
			}
			return stateMap[state] || '未知状态'
		}
	}
}
</script>

<style scoped>
.userinfo-header {
	display: flex;
	align-items: center;
	gap: 30rpx;
	padding: 20rpx 0;
	background-color: #E6E6FA;
}

.avatar {
	width: 80rpx;
	height: 80rpx;
	margin-left: 20rpx;
	border-radius: 50%;
}

.username {
	font-size: 32rpx;
	color: #4F4F4F;
	font-weight: 500;
}

.userinfo-content {
	padding: 20rpx;
	display: flex;
	flex-direction: column;
	gap: 30rpx;
	background-color: #E6E6FA;

}

.info-item {
	display: flex;
	align-items: center;
}

.label {
	color: #4F4F4F;
	margin-right: 20rpx;
}

.value {
	color: #4F4F4F;
}

.list {
	padding: 30rpx;
	box-sizing: border-box;
	padding-bottom: 60px;
	border-radius: 22rpx;

}

.tab {
	display: flex;
	justify-content: space-between;
	height: 70rpx;
	align-items: center;
}

/* 当前被选中的样式 */
.tab view {
	border-bottom: 1px solid #f5f5f5;
}

.tab .current {
	border-bottom: 1px solid #000;
}

.item {
	position: relative;
	display: flex;
	padding: 30rpx;
	box-sizing: border-box;
	border-bottom: 1px solid #eee;
	background-color: #E6E6FA;
	margin-bottom: 20rpx;
	border-radius: 15rpx;

}

.item image {
	width: 180rpx;
	height: 180rpx;
	border-radius: 12rpx;
	flex-shrink: 0;
}

.item view {
	flex: 1;
	margin-left: 30rpx;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
}

.item view view {
	margin: 0;
	line-height: 1.6;
	font-size: 28rpx;
}

/* 商品名称 */
.item view view:nth-child(1) {
	font-size: 30rpx;
	font-weight: 500;
	color: #333;
}

/* 下单时间 */
.item view view:nth-child(2) {
	color: #999;
	font-size: 26rpx;
}

/* 订单总额 */
.item view view:nth-child(3) {
	color: #ff5500;
	font-weight: bold;
}

.container {
	background: #ffffff;
}

.logout-btn {
	margin-top: 30rpx;
	width: 90%;
	height: 80rpx;
	line-height: 80rpx;
	text-align: center;
	background-color: #ff426e;
	color: #fff;
	border-radius: 40rpx;
	font-size: 28rpx;
}

.logout-btn:active {
	opacity: 0.8;
}

/* 修改折叠面板的样式 */
:deep(.uni-collapse-item__title) {
	background-color: #57310D !important;
}

:deep(.uni-collapse-item__wrap) {
	background-color: #57310D !important;
}

.confirm-btn {
	position: absolute;
	right: 20rpx;
	bottom: 20rpx;
	font-size: 24rpx;
	padding: 8rpx 20rpx;
	background-color: #FFF0F5;
	color: #8B7E66;
	border-radius: 25rpx;
	min-width: auto;
	line-height: 1.8;
	border: none;
	display: flex;
	align-items: center;
	gap: 4rpx;
}

.confirm-btn:active {
	opacity: 0.8;
}
</style>